<template>
  <div>
    <v-container>
      <v-row>
        <v-col
          cols="12"
        >
          <v-card
            class="mx-auto"
            max-width="650"
          >
            <v-form>
              <v-card-title>
                <span class="text-h4 text-md-h3">Relatório de Lançamentos</span>
              </v-card-title>

              <v-card-text>
                <v-container>
                  <v-row justify="center">
                    <v-col
                      cols="12"
                      sm="6"
                      md="12"
                    >
                      <v-text-field
                        v-model="company"
                        color="blue"
                        class="grey-label"
                        outlined
                        hide-details
                        label="Empresa"
                        prepend-icon="mdi-office-building-outline"
                        @keyup="company = company.toLowerCase().split(' ').map((s) =>s.charAt(0).toUpperCase() + s.substring(1)).join(' ')"
                      />
                    </v-col>

                    <v-col
                      cols="12"
                      sm="6"
                      md="12"
                    >
                      <v-text-field
                        v-model="plate"
                        v-mask="plateMask"
                        color="blue"
                        class="grey-label"
                        outlined
                        hide-details
                        label="Placa"
                        prepend-icon="mdi-account-outline"
                        onkeyup="this.value = this.value.toUpperCase()"
                      />
                    </v-col>
                    <v-col
                      cols="12"
                      md="6"
                    >
                      <v-select
                        v-model="state"
                        color="blue"
                        class="grey-label"
                        hide-details
                        :items="states"
                        prepend-icon="mdi-map-marker-outline"
                        label="Estado"
                        placeholder="Selecione"
                        outlined
                      />
                    </v-col>
                    <v-col
                      cols="12"
                      md="6"
                    >
                      <v-select
                        v-model="status"
                        color="blue"
                        class="grey-label"
                        prepend-icon="mdi-list-status"
                        hide-details
                        :items="statuses"
                        label="Status"
                        placeholder="Selecione"
                        outlined
                      />
                    </v-col>
                    <v-col
                      cols="12"
                      sm="6"
                    >
                      <v-text-field
                        v-model="computedDateFormatted1"
                        readonly
                        outlined
                        hide-details
                        max-width="100"
                        label="Início"
                        class="my-0 ms-2 pa-0"
                      />
                      <v-date-picker
                        v-model="date1"
                        color="amber"
                        locale="pt-BR"
                        no-title
                        class="ma-0 pa-0 mx-auto"
                      />
                    </v-col>
                    <v-col
                      cols="12"
                      sm="6"
                    >
                      <v-text-field
                        v-model="computedDateFormatted2"
                        readonly
                        outlined
                        hide-details
                        max-width="100"
                        label="Final"
                        class="my-0 ms-2 pa-0"
                      />
                      <v-date-picker
                        v-model="date2"
                        color="amber"
                        locale="pt-BR"
                        no-title
                        class="ma-0 pa-0 mx-auto"
                      />
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>

              <v-card-actions class="mt-n15 px-10 py-5">
                <v-btn
                  color="blue darken-3 white--text"
                  depressed
                  :disabled="!licences"
                  :loading="loading2"
                  append-icon="mdi-account"
                  @click="downloadPDF"
                >
                  Baixar
                  <v-icon
                    right
                    dark
                  >
                    mdi-file-document
                  </v-icon>
                </v-btn>
                <v-spacer />
                <v-btn
                  color="blue darken-3 white--text"
                  :loading="loading"
                  @click="generateReport(), loading = true"
                >
                  Gerar Relatório
                </v-btn>
              </v-card-actions>
            </v-form>
          </v-card>
        </v-col>
      </v-row>
      <v-row>
        <v-container v-if="showReport === true">
          <v-data-table
            :headers="headers"
            :items="licences"
            :footer-props="{ itemsPerPageOptions: [5, 10, -1] }"
          />
        </v-container>
      </v-row>
    </v-container>
    <vue-html2pdf
      :show-layout="false"
      :enable-download="true"
      :manual-pagination="false"
      :paginate-elements-by-height="14001"
      :filename="reportName"
      :pdf-quality="2"
      pdf-format="a4"
      pdf-orientation="landscape"
      pdf-content-width="100%"
      @startPagination="loading2 = true"
      @hasDownloaded="loading2 = false"
      ref="html2Pdf"
    >
      <section slot="pdf-content">
        <v-row>
          <v-col cols="12">
            <v-simple-table>
              <thead>
                <tr>
                  <th>Custo</th>
                  <th>Receita</th>
                  <th>Saldo</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>{{ totalCost }}</td>
                  <td>{{ totalRev }}</td>
                  <td>{{ totalProfit }}</td>
                </tr>
              </tbody>
            </v-simple-table>
          </v-col>
          <hr>
          <v-col cols="12">
            <v-simple-table dense>
              <thead>
                <tr>
                  <th>Data</th>
                  <th style="width:35%">Empresa</th>
                  <th>UF </th>
                  <th>Placa</th>
                  <th>Nº AET</th>
                  <th>Status</th>
                  <th>Custo</th>
                  <th>Valor Cobrado</th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="licence in licences"
                  :key="licence.AET"
                >
                  <td>{{ licence.DATA }}</td>
                  <td>{{ licence.EMPRESA }}</td>
                  <td>{{ licence.ESTADO }}</td>
                  <td>{{ licence.PLACA }}</td>
                  <td>{{ licence.AET }}</td>
                  <td>{{ licence.STATUS }}</td>
                  <td>{{ licence.CUSTOTAXA }}</td>
                  <td>{{ licence.VALORCOBRADO }}</td>
                </tr>
              </tbody>
            </v-simple-table>
          </v-col>
        </v-row>
      </section>
    </vue-html2pdf>
  </div>
</template>

<script>
  import axios from 'axios'
  import VueHtml2pdf from 'vue-html2pdf'

  export default {
    name: 'FinancialReport',

    components: {
      VueHtml2pdf
    },

    data: () => ({
      company: '',
      state: '',
      plate: '',
      status: '',
      table: '',
      dateFormatted: '',
      reportName: '',
      plateMask: 'AAA-#X##',
      showReport: false,
      licences: null,
      totals: null,
      totalCost: null,
      totalProfit: null,
      totalRev: null,
      repeated: false,
      loading: false,
      loading2: false,
      sevenAgo: new Date(new Date().setDate(new Date(new Date()).getDate() - 7)).toISOString().substr(0, 10),
      date1: new Date(new Date().getFullYear(), new Date().getMonth(), 1).toISOString().substr(0, 10), // first day of the month
      date2: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10), // today
      states: [
        { value: 'FEDERAL', text: 'FEDERAL' },
        { value: 'BA', text: 'Bahia' },
        { value: 'GO', text: 'Goiás' },
        { value: 'MG', text: 'Minas Gerais' },
        { value: 'MS', text: 'Mato Grosso do Sul' },
        { value: 'PE', text: 'Pernambuco' },
        { value: 'PR', text: 'Paraná' },
        { value: 'RJ', text: 'Rio de Janiero' },
        { value: 'RS', text: 'Rio Grande do Sul' },
        { value: 'SC', text: 'Santa Catarina' },
        { value: 'SP', text: 'São Paulo' },
        { value: 'TO', text: 'Tocantins' },
        { value: 'AC', text: 'Acre' },
        { value: 'AL', text: 'Alagoas' },
        { value: 'AM', text: 'Amazonas' },
        { value: 'AP', text: 'Amapá' },
        { value: 'CE', text: 'Ceará' },
        { value: 'DF', text: 'Distrito Federal' },
        { value: 'ES', text: 'Espirito Santo' },
        { value: 'MA', text: 'Maranhão' },
        { value: 'MT', text: 'Mato Grosso' },
        { value: 'PA', text: 'Pará' },
        { value: 'PB', text: 'Paraíba' },
        { value: 'PI', text: 'Piauí' },
        { value: 'RN', text: 'Rio Grande do Norte' },
        { value: 'RO', text: 'Rondônia' },
        { value: 'RR', text: 'Roraima' },
        { value: 'SE', text: 'Sergipe' },
      ],
      headers: [
        { text: 'Data', value: 'DATA' },
        { text: 'Empresa', value: 'EMPRESA' },
        { text: 'UF', value: 'ESTADO' },
        { text: 'Placa', value: 'PLACA' },
        { text: 'Nº AET', value: 'AET' },
        { text: 'Status', value: 'STATUS' },
        { text: 'Custo', value: 'CUSTOTAXA' },
        { text: 'Valor Cobrado', value: 'VALORCOBRADO' },
      ],
      statuses: [
        { value: 'A VENCER', text: 'A Vencer' },
        { value: 'TAXA PAGA', text: 'Taxa Paga' },
        { value: 'CONFERIR', text: 'Conferir' },
        { value: 'PAGO', text: 'Pago' },
        { value: 'A COBRAR', text: 'A Cobrar' },
        { value: 'VENCIDO', text: 'Vencido' },
        { value: 'RENEGOCIADO', text: 'Renegociado' },
        { value: 'NÃO COBRAR', text: 'Não Cobrar' },
        { value: 'A DEPOSITAR', text: 'A Depositar' },
        { value: 'ACERTO', text: 'Acerto' },
      ],
    }),

    computed: {
      computedDateFormatted1 () {
        return this.formatDate(this.date1)
      },
      computedDateFormatted2 () {
        return this.formatDate(this.date2)
      },
    },

    watch: {
      date1 () {
        this.dateFormatted = this.formatDate(this.date1)
      },
      date2 () {
        this.dateFormatted = this.formatDate(this.date2)
      },
      licences () {
        return this.reportName = 'Relatório ' + this.date2
      },
      plate () {
        return this.plateMask = 'AAA-#X##'
      }
    },

    methods: {
      formatDate (date) {
        if (!date) return null
        var [year, month, day] = date.split('-')
        return `${day}/${month}/${year}`
      },
      parseDate (date) {
        if (!date) return null
        const [day, month, year] = date.split('/')
        return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
      },
      generateReport () {
        return axios.get('https://sheetdb.io/api/v1/afxq0nl1f0gvg').then((r) => {
          this.tabela = r.data

          // var periodResult = this.tabela.filter(item => this.parseDate(item.DATA).replace(/-/g, '') >= this.date1.replace(/-/g, '') && this.parseDate(item.DATA).replace(/-/g, '') <= this.date1.replace(/-/g, ''))
          
          this.$nextTick(() => {
            var totalResult = this.tabela.filter(item => item.EMPRESA.includes(this.company) && item.PLACA.includes(this.plate) && item.ESTADO.includes(this.state)&& item.STATUS.includes(this.status) && this.parseDate(item.DATA).replace(/-/g, '') >= this.date1.replace(/-/g, '') && this.parseDate(item.DATA).replace(/-/g, '') <= this.date2.replace(/-/g, ''))

            this.licences = totalResult // returns filtered result

            const totalCost = this.licences.reduce((val, obj) => {
              if (!obj.CUSTOTAXA) {
                obj.CUSTOTAXA = 'R$ 0,00'
              }
              return (parseFloat(obj.CUSTOTAXA.replace('R$ ', '').replace('.', '').replace(',', '.')) + val)
            }, 0)

            const totalRev = this.licences.reduce((val, obj) => {
              if (!obj.VALORCOBRADO) {
                obj.VALORCOBRADO = 'R$ 0,00'
              }
              return (val + parseFloat(obj.VALORCOBRADO.replace('R$ ', '').replace('.', '').replace(',', '.')))
            }, 0)
            this.totalCost = new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(totalCost)
            this.totalRev = new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(totalRev)

            this.totalProfit = new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(totalRev.toFixed(2) - totalCost.toFixed(2))

            this.showReport = true
            this.loading = false
          })
        })
      },
      downloadPDF () {
        this.$refs.html2Pdf.generatePdf()
      },
    },
  }
</script>
